import Layout from '@/layout'

/* 隐藏页 */
const otherIndex = () => import('@/views/meta-data-manage/other/index')
const dataDetails = () => import('@/views/meta-data-manage/other/data-standard-details')

/* 非隐藏页 */
//  数据源管理
const Index = () => import('@/views/data-manage/data-source-manage/data-source-list')

export default [
    {
        path: '/',
        name: 'home',
        redirect: '/dataSource/list',
    },
    {
        path: '/dataSource',
        name: 'dataSource',
        component: Layout,
        redirect: '/dataSource/list',
        meta: {
            title: '数据管理'
        },
        children: [
            {
                path: 'list',
                name: 'list',
                component: Index,
                meta: {
                    title: '数据管理/数据源管理'
                }
            },

        ]
    },
    {
        path: '/other',
        name: 'other',
        component: otherIndex,
        meta: {
            title: '隐藏页面'
        },
        children: [
            {
                path: 'dataDetails',
                name: 'dataDetails',
                component: dataDetails,
                meta: {
                    title: '元数据-数据标准匹配详情'
                }
            },
        ]
    }

]


