 <template>
  <el-container class="layout">
    <el-header class="header">
      <div class="logo">
        <div class="logo-big">logo</div>
        <div class="logo-title">GDM</div>
      </div>
      <div class="menu">
        <div class="icon-menu" @click="isCollapse = !isCollapse">——</div>
        <ul class="menu-list">
          <li>数据标准管理</li>
          <li>元数据管理</li>
          <li>数据采集</li>
        </ul>
      </div>
      <div class="user">
        <div class="loged-user">
          <el-avatar
            src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            style="float: left;"
          ></el-avatar>
          <span class="loged-user-name">Marlon</span>
          <i class="el-icon-caret-bottom" style="float: right;margin: 17px 5px;"></i>
        </div>
        <!-- 悬浮：用户信息 -->
        <ul class="user-info">
          <li>修改密码</li>
          <li>退出</li>
        </ul>
      </div>
    </el-header>
    <el-container class="body">
      <el-aside width="auto" class="lt-aside">
        <el-menu
          v-if="Menu && Menu.subMenu && Menu.subMenu.length!==0"
          :default-active="$route.path"
          class="el-menu-vertical-demo"
          :collapse="isCollapse"
        >
          <template v-for="(item) in Menu.subMenu" :index="item.url">
            <el-submenu
              v-if="item.children && item.children.length"
              :key="item.url"
              :index="item.url"
            >
              <template slot="title">
                <i :class="item.icon"></i>
                <span>{{item.title}}</span>
              </template>
              <template v-for="(items) in item.children">
                <el-submenu
                  v-if="items.children && items.children.length"
                  :key="items.url"
                  :index="item.url"
                >
                  <template slot="title">
                    <i :class="items.icon"></i>
                    <span>{{items.title}}</span>
                  </template>
                  <template v-for="(itemss) in items.children">
                    <el-submenu
                      v-if="itemss.children && itemss.children.length"
                      :key="itemss.url"
                      :index="itemss.url"
                    >
                      <template slot="title">
                        <i :class="itemss.icon"></i>
                        <span>{{itemss.title}}</span>
                      </template>
                    </el-submenu>
                    <el-menu-item
                      v-else
                      :index="itemss.url"
                      :key="itemss.url"
                      @click="activeMenu(itemss)"
                    >
                      <i :class="itemss.icon"></i>
                      <span slot="title">{{itemss.title}}</span>
                    </el-menu-item>
                  </template>
                </el-submenu>
                <el-menu-item v-else :index="items.url" :key="items.url" @click="activeMenu(items)">
                  <i :class="items.icon"></i>
                  <span slot="title">{{items.title}}</span>
                </el-menu-item>
              </template>
            </el-submenu>
            <el-menu-item v-else :index="item.url" :key="item.url" @click="activeMenu(item)">
              <i :class="item.icon"></i>
              <span slot="title">{{items.title}}</span>
            </el-menu-item>
          </template>
        </el-menu>
      </el-aside>
      <el-main class="main">
        <!-- 面包屑 -->
        <div class="breadcrumb">
          <el-breadcrumb separator="/" class="breadcrumb-list">
            <el-breadcrumb-item>
              <a href="/">首页</a>
            </el-breadcrumb-item>
            <el-breadcrumb-item>活动列表</el-breadcrumb-item>
            <el-breadcrumb-item>活动详情</el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div class="content">
          <router-view />
        </div>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
// import layHeader from "./components/header.vue";
export default {
  components: {
    // layHeader
  },
  data() {
    return {
      currTabIndex: 0,
      isCollapse: false,
    };
  },
  computed: {
    Menu() {
      return global.MENU;
    }
  },
  watch: {
    $route(to, from) {
      // console.log(to,from)
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      // console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      // console.log(key, keyPath);
    },
    // 菜单激活
    activeMenu(item) {
      if (this.$route.path !== item.url) {
        this.$router.replace({
          path: item.url
        });
      }
    }
  },
};
</script>
<style lang='scss'>
@import "~@/assets/css/platform/gst/config.scss";
.layout {
  width: 100%;
  height: 100%;
  transition: width 0.3s ease;
}
.header {
  box-sizing: border-box;
  overflow: hidden;
  background: $header_bg;
  color: $header_color;
  .logo {
    float: left;
    height: 60px;
    overflow: hidden;
    margin: 0 20px 0 0;
    & > div {
      float: left;
    }
    &-big {
      box-sizing: border-box;
      width: 50px;
      height: 50px;
      border-radius: 5px;
      line-height: 48px;
      border: 1px solid red;
      margin: 4px auto;
      margin-right: 30px;
      text-align: center;
    }
    &-title {
      box-sizing: border-box;
      border: 1px solid red;
      margin: 20px auto;
    }
  }
  .menu {
    float: left;
    overflow: hidden;
    margin: 15px 0px;
    .icon-menu {
      float: left;
      margin: 0 15px;
    }
    &-list {
      float: left;
      overflow: hidden;
      li {
        float: left;
        box-sizing: border-box;
        border: 1px solid red;
        padding: 5px 15px;
        margin: 0 10px;
        cursor: pointer;
      }
    }
  }
  .user {
    float: right;
    width: 150px;
    height: 100%;
    box-sizing: border-box;
    overflow: hidden;
    .loged-user {
      height: 45px;
      margin: 6px 0;
      cursor: pointer;
      &-name {
        display: inline-block;
        position: relative;
        top: -13px;
        left: 10px;
        width: 75px;
        height: 40px;
        line-height: 48px;
        margin: 12px 0px;
        overflow: hidden;
        text-align: center;
      }
    }
    &-info {
      display: none;
      overflow: hidden;
      position: absolute;
      width: 150px;
      li {
        height: 30px;
        line-height: 28px;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid red;
        text-align: center;
        cursor: pointer;
      }
    }
  }
}
.body {
  height: 100%;
  overflow: hidden;
}
.lt-aside {
  box-sizing: border-box;
  background: $lt_bg;
  // transition: width 0.3s ease;
  color: $lt_font;
  /deep/ .el-menu {
    background-color: transparent;
    border-right: 0;
  }
  /deep/ .el-submenu__title {
    color: $lt_font;
    &:hover {
      background-color: $lt_menu_bg;
      color: $lt_memu_color;
      i {
        color: $lt_memu_color;
      }
    }
  }
  /deep/ .el-menu-item {
    color: $lt_font;
    &:hover {
      background-color: $lt_menu_bg;
      color: $lt_memu_color;
    }
    &:focus {
      background-color: $lt_menu_bg_active;
      color: $lt_memu_color;
    }
  }
  /deep/ .el-menu-item.is-active {
    background-color: $lt_menu_bg_active;
    color: $lt_memu_color;
  }
}
.main {
  box-sizing: border-box;
  // border: 1px solid red;
  padding: 0;
  .breadcrumb {
    width: 100%;
    height: 30px;
    box-sizing: border-box;
    border: 1px solid red;
    padding: 0 10px;
    &-list {
      margin: 7px 2px;
    }
  }
  .content {
    padding: 20px;
    min-width: 1300px;
    width: calc(100% - 1px);
    height: calc(100% - 30px);
    box-sizing: border-box;
    border: 1px solid red;
    overflow: auto;
  }
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
</style>


