import Vue from 'vue'
import Router from 'vue-router'
import service from 'utils/request'
import { Message } from "element-ui";


// 404、500、无权限访问

Vue.use(Router)
/* 
  元数据管理：meta-data-manage
  数据管理：data-manage
  数据采集：data-collection
*/
let PROJECT = 'data-collection';

if (process.env.NODE_ENV === 'production') {
  PROJECT = process.env.Project_key.replace(/\"/g, '')
}
// 项目配置
export const ProjectKey = PROJECT;
let routes = require(`./config/${ProjectKey}`).default;

const router = new Router({
  // mode: 'history',
  routes
});

router.beforeEach((to, from, next) => {
  // if (MENU && MENU.subMenu && MENU.length) {
  //   router.addRoutes(MENU.subMenu)
  //   router.options.routes.concat(MENU.subMenu)
  // }
  next({ to, replace: true })
})

  ; (() => {
    service({
      url: `static/${ProjectKey}.json`,
      method: 'get',
    }).then(res => {
      if (res && res.data) {
        global.MENU = res.data
      } else {
        Message({
          showClose: true,
          message: '获取菜单失败！',
          type: "error"
        });
      }
    })
  })();
export default router



